@import "color";
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,400;1,300&display=swap');

.pop_up {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100vh;
  justify-content: center;
  backdrop-filter: blur(10px);
  
  align-items: center;

  > .pop_up_content{
    background-color: rgb(240, 239, 239);
    border: 1px solid black;
    border-radius: 1rem;
    padding: 1rem;
    height: 50%;
  width: 70%;
  display: flex;
  
  flex-direction: column;
  
  > .btn{
    display: flex;
     justify-content: flex-end;
     cursor: pointer;
   
  }
  > .content{
    
   
    margin-top: 2rem;
    >img{
      width: 150px;
      height: 150px;
      object-fit: contain;
      border-radius: 1rem;
    }
  }
  }
}

.home{
    display: flex;
    justify-content: center;
   padding: 3rem 1rem;
     flex-wrap: wrap;
     
}
.chat{
  position: fixed;
     right:    1rem;
     bottom:   1rem;
     font-size: 1.3rem;
     font-family: 'Source Sans Pro', sans-serif;
     display: flex;
     justify-content: center;
     align-items: center;
     padding: 1rem 2rem;
     border: none;
     border-radius: 2rem;
     background-color: rgb(21, 132, 60);
     color: white;
     cursor: not-allowed;
     >.icon{
    margin-right: 1rem;
    font-size: 1.6rem;
     }
 }
.productCard{

  width: 350px;
  margin: 10px;
  border-radius: 5px;
  padding: 2rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  transition: all 0.5s;
   >.detailes{
    display: flex;
    padding: 0 3rem;
   width: 100%;
    justify-content: space-between;
    align-items: center;
    >p{
      cursor: pointer;
      font-family: 'Source Sans Pro', sans-serif;
     
        &:hover{
          color: $secondary;
            
      }
    }
   }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 30px;
      cursor: pointer;
     
    }
    h4{
      margin: 1rem;
    }
    >p{
      margin-top: 1rem;
      font-family: 'Source Sans Pro', sans-serif;
    
    }
    button{
      cursor: pointer;
      background-color: black;
      padding: 1rem;
      border: none;
      border-radius: 5px;
      font-weight: 500;
      transition: all 0.5s;
      color: white;
      width: 100%;
      &:hover{
    background-color: rgb(94, 90, 90);
      }
    }
    &:hover{
     transform: scale(1.05);
        }
}
@media screen and (max-width: 820px){
  .chat{
    font-size: 1rem;
    padding: .7rem 1.4rem;
  }
}